import {
  Directive,
  ElementRef,
  Input,
  OnChanges,
  SimpleChanges,
} from '@angular/core';
import {textEllipsis} from './text-ellipsis';
import {select} from 'd3';


@Directive({selector: 'svg text[adaptTextEllipsis]'})
export class AdaptTextEllipsisDirective implements OnChanges {
  @Input() text: string;
  @Input() width: number;

  constructor(private _el: ElementRef) {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes) {
      const el = select(this._el.nativeElement);

      textEllipsis(el, this.text, this.width);
    }
  }
}
